import React from "react"
import styled from "styled-components"

import Game from "./components/game"
import { connect } from "react-redux"
import { action } from "./redux/actions"
import { GODOWN, GOUP, GORIGHT, GOLEFT } from "./redux/constants"
import media from "./media-query/media"

const Page = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: center;

  ${media.lessThan("small")`
    user-select: none;
  `};
`

class App extends React.Component {
  componentDidMount() {
    document.addEventListener("keydown", this.checkKeyPress)
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.checkKeyPress)
  }

  checkKeyPress = e => {
    e.preventDefault()
    const key = e.key
    if (!e.repeat && this.props.isPlaying)
      if (key === "ArrowDown") {
        this.props.action(GODOWN)
      } else if (key === "ArrowUp") {
        this.props.action(GOUP)
      } else if (key === "ArrowLeft") {
        this.props.action(GOLEFT)
      } else if (key === "ArrowRight") {
        this.props.action(GORIGHT)
      }
  }

  render() {
    return (
      <Page id="MyGame">
        <Game />
      </Page>
    )
  }
}

const mapStateToProps = state => {
  return {
    isPlaying: state.IsPlaying,
  }
}

export default connect(
  mapStateToProps,
  { action }
)(App)
