import React from "react"
import media from "../media-query/media"
import Cells from "./cells"
import Swipe from "react-easy-swipe"
import styled, { css } from "styled-components"

import { connect } from "react-redux"
import { action } from "../redux/actions"
import { GOUP, GODOWN, GOLEFT, GORIGHT } from "../redux/constants"
import { ANIMATIONTIME } from "../utils/constants"

const backgroundColor = css`
  background-color: #bbada1;
`

const GameWindowContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  transition: opacity 2s ease-in;

  ${props => (props.isPlaying ? "" : "opacity: 0.3;")}
`

const GameWindow = styled.div`
  ${backgroundColor};
  width: 350px;
  height: 350px;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  position: relative;
  transition: width ${ANIMATIONTIME}ms ease-in,
    height ${ANIMATIONTIME}ms ease-in;

  ${media.lessThan("xSmall")`
    width: 250px;
    height: 250px;
  border-radius: 15px;
  `}

  ${media.between("xSmall", "small")`
    padding: 0;
    width: 280px;
    height: 280px;
      ${props => props.fullScreen}
  `}
`

class Game extends React.Component {
  state = {
    isSwaping: false,
    fullScreen: "",
  }

  componentDidMount() {
    document.getElementById("GameWindow").addEventListener("touchmove", e => {
      e.preventDefault()
    })
  }

  onSwipeMove = position => {
    if (Math.abs(position.x) > 30 || Math.abs(position.y) > 30) {
      if (this.state.isSwaping === false) {
        this.setState(
          {
            isSwaping: true,
          },
          () => this.move(position)
        )
      }
    }
  }

  onSwipeEnd = () => {
    this.setState({
      isSwaping: false,
    })
  }

  move = position => {
    let direction
    if (Math.abs(position.x) > Math.abs(position.y)) {
      if (position.x > 0) {
        direction = GORIGHT
      } else {
        direction = GOLEFT
      }
    } else {
      if (position.y > 0) {
        direction = GODOWN
      } else {
        direction = GOUP
      }
    }
    this.props.action(direction)
  }

  disableButton = () => {
    if (
      this.props.undo > 0 &&
      this.props.numbers !== this.props.prevNumbers &&
      this.props.numbers !== this.props.prevPrevNumbers
    ) {
      return false
    } else {
      return true
    }
  }

  getButtonStyles = () => {
    if (
      this.props.undo > 0 &&
      this.props.numbers !== this.props.prevNumbers &&
      this.props.numbers !== this.props.prevPrevNumbers
    ) {
      return ""
    } else {
      return `opacity: 0.5; cursor: default;`
    }
  }

  render() {
    return (
      <>
        <Swipe
          onSwipeStart={this.onSwipeStart}
          onSwipeMove={this.onSwipeMove}
          onSwipeEnd={this.onSwipeEnd}
        >
          <GameWindowContainer isPlaying={this.props.isPlaying}>
            <GameWindow fullScreen={this.state.fullScreen} id="GameWindow">
              <Cells extend={false} />
            </GameWindow>
          </GameWindowContainer>
        </Swipe>
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    undo: state.AmountOfUnDos,
    numbers: state.Numbers,
    prevNumbers: state.PrevNumbers,
    prevPrevNumbers: state.PrevPrevNumbers,
    highestNumber: state.HighestNumber,
    isPlaying: state.IsPlaying,
  }
}

export default connect(
  mapStateToProps,
  { action }
)(Game)
